import React from "react";

function Content1() {
  return (
    <div className="container-fluid bg__blue px-0">
      <div className="container h_main d-flex align-items-center">
        <div className="row d-flex align-items-center">
          <div data-aos="slide-right" className="col-md-5">
            <img className="w-100" src="assets/head2.png" alt="" />
          </div>
          <div data-aos="slide-left" className="col-md-7 text-right">
            <h2 className="text-right">What are Big Bros</h2>
            <p className="line">
              Big Bros is a collection of 10,000 tough Bros chilling on the
              Ethereum blockchain. The Big Bros are a combination of hand-drawn
              traits randomly designed together and generated through a computer
              program. Our mission as Bros is to grow as much as we can and
              support each Bro through rewards and giveaways.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Content1;
